import { Dialog, IconButton } from "@material-ui/core"
import { Assignment, CheckCircle, RemoveCircle, WatchLater } from "@material-ui/icons"
import { useState } from "react";
import { BrowserRouter as Router, Route, Link, useHistory, useParams } from "react-router-dom";



function MuutaTilannetta(props) {

    let apuLista = props.tiketit
    let kohde = props.tilanneMuutosKohde

    const tilannePaivitys = (x) => {
        console.log(kohde, 'tartu listaan ', apuLista[kohde].tilanne)
        apuLista[kohde].tilanne = x
        props.setTiketit(apuLista)
        props.tallennaLista()
        props.setAuki(false)
    }

    return (
        <Dialog
            open={props.auki}
            onBackdropClick={() => { props.setAuki(false) }}
        >
            <table>
                <tr>
                    <td>
                        <IconButton
                            variant="contained"
                            color="primary"
                            style={{ marginTop: 40, marginRight: 5 }}
                            onClick={() => {
                                tilannePaivitys("Jonossa")
                            }}
                        >
                            <Assignment style={{ fontSize: 120, color: 'grey' }} />
                        </IconButton>

                    </td>
                    <td>
                        <IconButton
                            variant="contained"
                            color="primary"
                            style={{ marginTop: 40, marginRight: 5 }}
                            onClick={() => {
                                tilannePaivitys("Odottaa toimia")
                            }}

                        >
                            <WatchLater style={{ fontSize: 120, color: 'yellow' }} />
                        </IconButton>
                    </td>
                </tr>
                <tr>
                    <td>
                        <IconButton
                            variant="contained"
                            color="primary"
                            style={{ marginTop: 40, marginRight: 5 }}
                            onClick={() => {
                                tilannePaivitys("Ei ratkaisua")
                            }}
                        >
                            <RemoveCircle style={{ fontSize: 120, color: 'red' }} />
                        </IconButton>

                    </td>
                    <td>
                        <IconButton
                            variant="contained"
                            color="primary"
                            style={{ marginTop: 40, marginRight: 5 }}
                            onClick={() => {
                                tilannePaivitys("Ratkaistu")
                            }}
                        >
                            <CheckCircle style={{ fontSize: 120, color: 'green' }} />
                        </IconButton>
                    </td>
                </tr>
            </table>
        </Dialog>
    )
}

export default MuutaTilannetta