import { Container, Icon, IconButton, List, ListItem, ListItemIcon, ListItemText, TextField, Typography } from "@material-ui/core"
import { BrowserRouter as Router, Route, Link } from "react-router-dom";
import DeleteForeverIcon from '@material-ui/icons/DeleteForever';
import EditIcon from '@material-ui/icons/Edit';
import DateFnsUtils from "@date-io/date-fns";
import { DateTimePicker, MuiPickersUtilsProvider } from "@material-ui/pickers";
import { fi } from 'date-fns/locale'
import { format } from 'date-fns'
import { Assignment, Close, CheckCircle, PriorityHigh, WatchLater, Cancel } from "@material-ui/icons";
import { useRef, useState } from "react";

function Hae(props) {

    const [hakuLista, setHakuLista] = useState([])
    let lista = props.tiketit
    let apuHakuLista = []
    const hakuehto = useRef();

    const haeTiketeista = () => {
        console.log(lista)
        console.log('hae', hakuehto.current.value)
        lista.map((rivi, idx) => {
            if (rivi.otsikko.toLowerCase().includes(hakuehto.current.value.toLowerCase())) {
                apuHakuLista.push(rivi)
            } else if (rivi.ratkaisu.toLowerCase().includes(hakuehto.current.value.toLowerCase())){
                apuHakuLista.push(rivi)
            } 
        })
        console.log('apulista: ', apuHakuLista)
        setHakuLista(apuHakuLista)
        console.log('tilamuuttujalista: ', apuHakuLista)
    }

    return (
        <Container>

            <TextField
                name="hakuehto"
                variant="outlined"
                label='Hae'
                fullWidth={true}
                inputRef={hakuehto}
                style={{
                    marginBottom: 15,
                    marginTop: 15
                }}
                onKeyPress={ (e) => {
                    if (e.key === "Enter"){
                        haeTiketeista()
                    }}
                }
            />

            <List>

                {
                    hakuLista.map((tiketti, idx) => {

                        let apuKuvaus = tiketti.otsikko;
                        let apuAika = new Date();

                        if(tiketti.luomispvm){
                            apuAika = format(tiketti.luomispvm, "dd.MM.y HH.mm");
                        } 
                        let apuTiedot = `[Luotu: ${apuAika}] ${tiketti.ratkaisu}`
                        let tilanneKuvake = <CheckCircle style={{ color: 'green' }} />

                        switch (tiketti.tilanne) {
                            case "Jonossa":
                                tilanneKuvake = <Assignment style={{ color: 'grey' }} />
                                break;
                            case "Odottaa toimia":
                                tilanneKuvake = <WatchLater style={{ color: 'yellow' }} />
                                break;
                            case "Ratkaistu":
                                tilanneKuvake = <CheckCircle style={{ color: 'green' }} />
                                break;
                            case "Ei ratkaisua":
                                tilanneKuvake = <Cancel style={{ color: 'red' }} />
                                break;
                        }


                        return (
                            <ListItem key={idx}>

                                <IconButton>
                                    {tilanneKuvake}
                                </IconButton>

                                <ListItemText primary={apuKuvaus} secondary={apuTiedot} />
                                <ListItemIcon>
                                    <IconButton
                                        component={Link}
                                        to={`/muokkaa/${idx}`}>
                                        <EditIcon />
                                    </IconButton>
                                    <IconButton
                                        component={Link}
                                        to={`/poista/${tiketti.id}`}
                                    >
                                        <DeleteForeverIcon />
                                    </IconButton>
                                </ListItemIcon>
                                <PriorityHigh style={{ color: 'red' }} />
                            </ListItem>
                        )
                    })}

            </List>

        </Container>
    )

} 
export default Hae