import { AppBar, Container, Fab, IconButton, Tab, Tabs, Typography } from '@material-ui/core';
import { useEffect, useState } from 'react';
import { BrowserRouter as Router, Route, Link } from 'react-router-dom';
import Seurattavat from './components/Seurattavat';
import TikettiLista from './components/TikettiLista';
import UusiTiketti from './components/UusiTiketti'
import Poista from './components/Poista'
import Muokkaa from './components/Muokkaa'
import Kotisivu from './components/Kotisivu'
import { Add, Search } from '@material-ui/icons';
import Hae from './components/Hae';
import MuutaTilannetta from './components/MuutaTilannetta';

function App() {

  const [tiketit, setTiketit] = useState([{}]);
  const [auki, setAuki] = useState(false);
  const [tilanneMuutosKohde, setTilanneMuutosKohde] = useState(0);
  const [value, setValue] = useState(0);

  


  const tallennaLista = () => {

    localStorage.setItem("tiketit", JSON.stringify(tiketit));

  }

  const avaaLista = () => {

    if (localStorage.getItem("tiketit")) {

      setTiketit(JSON.parse(localStorage.getItem("tiketit")));

    } else {

      setTiketit([]);

    }

  }

  useEffect(() => {

    avaaLista();

  }, [])


  useEffect(() => {

    tallennaLista();

  }, [tiketit, setTiketit])

  const muutaTab = (e, uusiArvo) => {
    setValue(uusiArvo);
  };

  return (

    <Container maxWidth='sm'>
      <Router>

        <Typography 
          variant="h5" 
          align='center' 
          color='primary' 
          style={{fontWeight: 'bold', 
          letterSpacing: '16px',
          transform: 'scale(0.9, 1.4)',
          lineHeight: '2'
        }}
          >
            TOIMEKSIANNOT
          </Typography>


        <AppBar position="static">
        <Tabs
        value={value}
        indicatorColor="primary"
        centered
        onChange={muutaTab}
        aria-label="Valittu sivu"
      >
        <Tab 
          label="Koti" 
          component={Link}
          to="/"
          />
        <Tab label="Tikettilista"
        component={Link}
        to="/tikettilista"
        />
        <Tab label="Seurattavat" 
        component={Link}
        to="/seurattavat/"
        />
      </Tabs>
        </AppBar>


        <Route path='/' exact>
          <Kotisivu tiketit={tiketit} setTiketit={setTiketit} auki={auki} setAuki={setAuki} setTilanneMuutosKohde={setTilanneMuutosKohde} />
        </Route>

        <Route path='/tikettilista'>
          <TikettiLista tiketit={tiketit} setTiketit={setTiketit} auki={auki} setAuki={setAuki} setTilanneMuutosKohde={setTilanneMuutosKohde} />
        </Route>

        <Route path='/uusi'>
          <UusiTiketti tiketit={tiketit} setTiketit={setTiketit} />
        </Route>

        <Route path='/seurattavat'>
          <Seurattavat tiketit={tiketit} setTiketit={setTiketit} auki={auki} setAuki={setAuki} setTilanneMuutosKohde={setTilanneMuutosKohde} />
        </Route>

        <Route path="/poista/:id" >
          <Poista tiketit={tiketit} setTiketit={setTiketit} />
        </Route>

        <Route path="/muokkaa/:indeksi" >
          <Muokkaa tiketit={tiketit} setTiketit={setTiketit} tallennaLista={tallennaLista} />
        </Route>

        <Route path='/hae'>
          <Hae tiketit={tiketit} setTiketit={setTiketit} />
        </Route>

        <MuutaTilannetta tilanneMuutosKohde={tilanneMuutosKohde} auki={auki} setAuki={setAuki} tiketit={tiketit} setTiketit={setTiketit} tallennaLista={tallennaLista} />

        <IconButton
          component={Link}
          to="/hae/"
          variant="contained"
          color="primary"
          style={{ marginTop: 40, position: 'fixed', bottom: '90px', right: '26px' }}
        >
          <Search style={{ fontSize: 30 }} />
        </IconButton>

        <Fab
          color="primary"
          size='large'
          component={Link}
          to="/uusi/"
          style={{ marginTop: 15, position: 'fixed', bottom: '20px', right: '26px' }}
        >
          <Add style={{ fontSize: 40 }} />
        </Fab>

      </Router>
    </Container>
  );
}

export default App;

