import { Container, IconButton, List, ListItem, ListItemIcon, ListItemText, ListSubheader, Typography } from "@material-ui/core";
import { Assignment, CheckCircle, PriorityHigh, RemoveCircle, WatchLater } from "@material-ui/icons";
import { BrowserRouter as Router, Route, Link } from "react-router-dom";
import DeleteForeverIcon from '@material-ui/icons/DeleteForever';
import EditIcon from '@material-ui/icons/Edit';
import { format } from 'date-fns'

function Seurattavat(props) {
    let ratkaistut = 0

    return (
        <Container>
        <List>
            {
            props.tiketit.map((tiketti, idx) => {
                if(tiketti.tilanne === "Ratkaistu"){
                    ratkaistut++
                }
            })
            }

<ListSubheader>Uusimmat tehtävät (Ratkaistuja tikettejä: {ratkaistut}/{props.tiketit.length}) </ListSubheader>

            {props.tiketit.map((tiketti, idx) => {
                let apuAika = new Date();

                if(tiketti.luomispvm){
                    apuAika = format(tiketti.luomispvm, "dd.MM.y HH.mm");
                } 

                let apuKuvaus = tiketti.otsikko;
                let apuTiedot = `[Luotu: ${apuAika}] ${tiketti.ratkaisu}`
                let tilanneKuvake = <CheckCircle style={{ color: 'green' }} />
                let seurausikoni = <PriorityHigh style={{ color: '#e3e3e3' }} />

                switch (tiketti.tilanne) {
                    case "Jonossa":
                        tilanneKuvake = <Assignment style={{ color: 'grey' }} />
                        break;
                    case "Odottaa toimia":
                        tilanneKuvake = <WatchLater style={{ color: 'yellow' }} />
                        break;
                    case "Ratkaistu":
                        tilanneKuvake = <CheckCircle style={{ color: 'green' }} />
                        break;
                    case "Ei ratkaisua":
                        tilanneKuvake = <RemoveCircle style={{ color: 'red' }} />
                        break;
                }

                if (tiketti.seurattava === true) {
                    seurausikoni = <PriorityHigh style={{ color: 'red' }} />
                }

                if (idx < 3) {
                    return (
                        <ListItem key={idx}>
                            <IconButton
                                onClick={() => {
                                    console.log('avataan', idx)
                                    props.setTilanneMuutosKohde(idx)
                                    props.setAuki(true)
                                    console.log('Avattu?', props.auki)
                                }}
                                style={{ marginRight: 5 }}>
                                {tilanneKuvake}
                            </IconButton>

                            <ListItemText primary={apuKuvaus} secondary={apuTiedot} />
                            <ListItemIcon>
                                <IconButton
                                    component={Link}
                                    to={`/muokkaa/${idx}`}>
                                    <EditIcon />
                                </IconButton>
                                <IconButton
                                    component={Link}
                                    to={`/poista/${tiketti.id}`}
                                >
                                    <DeleteForeverIcon />
                                </IconButton>
                                <IconButton component={Link} to={`/muokkaa/${idx}`}>
                                    {seurausikoni}
                                </IconButton>
                            </ListItemIcon>
                        </ListItem>

                    )
                }
            })}

<ListSubheader>Keskeneräiset seurattavat:</ListSubheader>

{props.tiketit.map((tiketti, idx) => {

    let apuKuvaus = tiketti.otsikko;
    let apuAika = new Date();

    if(tiketti.luomispvm){
        apuAika = format(tiketti.luomispvm, "d.MM.y HH.mm");
    } 
    let apuTiedot = `[Luotu: ${apuAika}] ${tiketti.ratkaisu}`
    let tilanneKuvake = <CheckCircle style={{ color: 'green' }} />
    let seurausikoni = <PriorityHigh style={{ color: '#e3e3e3' }} />

    switch (tiketti.tilanne) {
        case "Jonossa":
            tilanneKuvake = <Assignment style={{ color: 'grey' }} />
            break;
        case "Odottaa toimia":
            tilanneKuvake = <WatchLater style={{ color: 'yellow' }} />
            break;
        case "Ratkaistu":
            tilanneKuvake = <CheckCircle style={{ color: 'green' }} />
            break;
        case "Ei ratkaisua":
            tilanneKuvake = <RemoveCircle style={{ color: 'red' }} />
            break;
    }

    if (tiketti.seurattava === true) {
        seurausikoni = <PriorityHigh style={{ color: 'red' }} />
    }

    if (tiketti.seurattava === true && tiketti.tilanne !== "Ratkaistu" && tiketti.tilanne !== "Ei ratkaisua") {
        return (
            <ListItem key={idx}>
                <IconButton
                    onClick={() => {
                        console.log('avataan', idx)
                        props.setTilanneMuutosKohde(idx)
                        props.setAuki(true)
                        console.log('Avattu?', props.auki)
                    }}
                    style={{ marginRight: 5 }}>
                    {tilanneKuvake}
                </IconButton>

                <ListItemText primary={apuKuvaus} secondary={apuTiedot} />
                <ListItemIcon>
                    <IconButton
                        component={Link}
                        to={`/muokkaa/${idx}`}>
                        <EditIcon />
                    </IconButton>
                    <IconButton
                        component={Link}
                        to={`/poista/${tiketti.id}`}
                    >
                        <DeleteForeverIcon />
                    </IconButton>
                    <IconButton component={Link} to={`/muokkaa/${idx}`}>

                        {seurausikoni}
                    </IconButton>
                </ListItemIcon>
            </ListItem>

        )
    }
})}


        </List>
        </Container>
    )
}


export default Seurattavat