import { Button, Typography } from '@material-ui/core';
import { Link, useHistory, useParams } from 'react-router-dom';

function Poista(props) {

    const history = useHistory();
    const { id } = useParams();

    const poistettavaTiketti = props.tiketit.filter((tiketti) => {
            return (tiketti.id === id)  ;
    })[0];

    const kasitteleLomake = (e) => {

        e.preventDefault();

        let tiketitApu = props.tiketit.filter((tiketti) => {
            return (tiketti.id !== id);
        });
    
        props.setTiketit([...tiketitApu]);
    
        history.push("/Tikettilista");

    }

    return (
        <form onSubmit={kasitteleLomake}>

        <Typography 
            variant="h5" 
            align='center'
            centered style={{marginTop:'30px'}}
        >Vahvista tehtävän poisto</Typography>

        <Typography
            align='center'
            style={{marginBottom : 30}}
        >Haluatko varmasti poistaa tiketin "{poistettavaTiketti.otsikko}"?</Typography>

        <Button 
            type="submit"
            variant="contained"
            color="primary"
            fullWidth
            style={{marginBottom : 10}}
        >Poista tehtävä</Button>

        <Button 
            component={Link}
            to="/Tikettilista"
            variant="contained"
            color="default"
            fullWidth
        >Peruuta</Button>

        </form>
    )
}

export default Poista;