import { Button, FormControl, FormControlLabel, InputLabel, MenuItem, Select, Switch, TextField } from "@material-ui/core"
import { useRef, useState } from "react";
import DateFnsUtils from "@date-io/date-fns";
import { DateTimePicker, MuiPickersUtilsProvider } from "@material-ui/pickers";
import { fi } from 'date-fns/locale'
import { BrowserRouter as Router, Route, Link, useHistory, useParams } from "react-router-dom";
import { format } from 'date-fns'


function Muokkaa(props) {

    const { indeksi } = useParams();

    let apuOtsikko = props.tiketit[indeksi].otsikko;
    let apuRatkaisu = props.tiketit[indeksi].ratkaisu;
    let apuTilanne = props.tiketit[indeksi].tilanne;

    const history = useHistory();
    const muokattuotsikko = useRef();
    const muokatturatkaisu = useRef();
    const [seurattava, setSeurattava] = useState(props.tiketit[indeksi].seurattava)

    const lomakeKasittelija = (e) => {

        e.preventDefault();

        let lista = props.tiketit;

        let apuMuutos = {
            id: props.tiketit[indeksi].id,
            luomispvm: props.tiketit[indeksi].luomispvm,
            tilanne: props.tiketit[indeksi].tilanne,
            otsikko: muokattuotsikko.current.value,
            ratkaisu: muokatturatkaisu.current.value,
            seurattava: seurattava
        }

        lista[indeksi] = apuMuutos;
        props.setTiketit([...lista])
        props.tallennaLista()
        history.push("/tikettilista")
    }

    const vipuKasittelija = (e) => {
        setSeurattava(!seurattava)
    }

    return (
        <form onSubmit={lomakeKasittelija}>

            <FormControl>

            </FormControl>

            <TextField
                name="ongelmankuvaus"
                variant="outlined"
                label={`Ongelman kuvaus`}
                defaultValue={apuOtsikko}
                fullWidth={true}
                inputRef={muokattuotsikko}
                style={{
                    marginBottom: 15,
                    marginTop: 15
                }}
            />

            <TextField
                name="tehdyt toimet"
                variant="outlined"
                label={`Tehdyt toimet:`}
                defaultValue={apuRatkaisu}
                fullWidth={true}
                multiline={true}
                rows='8'
                inputRef={muokatturatkaisu}
                style={{
                    marginBottom: 15,
                    marginTop: 15
                }}
            />

            <FormControlLabel
                label="Seurattava"
                control={

                    <Switch
                        checked={seurattava}
                        name="seurantaVipu"
                        color="primary"
                        value={seurattava}
                        onChange={vipuKasittelija}
                    />
                }

            />

            <Button
                type="submit"
                variant="contained"
                color="primary"
                style={{ marginTop: 15 }}
                fullWidth={true}>
                Muokkaa
            </Button>

            <Button
                component={Link}
                to="/tikettilista"
                variant="contained"
                color="default"
                style={{ marginTop: 15 }}
                fullWidth={true}>
                Peruuta
        </Button>
        </form>
    )
}

export default Muokkaa