import { Icon, IconButton, List, ListItem, ListItemIcon, ListItemText, Typography } from "@material-ui/core"
import { BrowserRouter as Router, Route, Link } from "react-router-dom";
import DeleteForeverIcon from '@material-ui/icons/DeleteForever';
import EditIcon from '@material-ui/icons/Edit';
import DateFnsUtils from "@date-io/date-fns";
import { DateTimePicker, MuiPickersUtilsProvider } from "@material-ui/pickers";
import { fi } from 'date-fns/locale'
import { format } from 'date-fns'
import { Assignment, CheckCircle, PriorityHigh, WatchLater, RemoveCircle } from "@material-ui/icons";

function TikettiLista(props) {


    return (
            <List>
                {props.tiketit.map((tiketti, idx) => {

                    let apuKuvaus = tiketti.otsikko;
                    let apuAika = new Date();

                    if(tiketti.luomispvm){
                        apuAika = format(tiketti.luomispvm, "dd.MM.y HH.mm");
                    } 
                    let apuTiedot = `[Luotu: ${apuAika}] ${tiketti.ratkaisu}`
                    let tilanneKuvake = <CheckCircle style={{ color: 'green' }} />
                    let seurausikoni = <PriorityHigh style={{ color: '#e3e3e3' }} />

                    switch (tiketti.tilanne) {
                        case "Jonossa":
                            tilanneKuvake = <Assignment style={{ color: 'grey' }} />
                            break;
                        case "Odottaa toimia":
                            tilanneKuvake = <WatchLater style={{ color: 'yellow' }} />
                            break;
                        case "Ratkaistu":
                            tilanneKuvake = <CheckCircle style={{ color: 'green' }} />
                            break;
                        case "Ei ratkaisua":
                            tilanneKuvake = <RemoveCircle style={{ color: 'red' }} />
                            break;
                    }

                    if (tiketti.seurattava === true) {
                        seurausikoni = <PriorityHigh style={{ color: 'red' }} />
                    } 

                    

                    return (
                        <ListItem key={idx}>

                            <IconButton
                                onClick={() => {
                                    console.log('avataan', idx)
                                    props.setTilanneMuutosKohde(idx)
                                    props.setAuki(true)
                                    console.log('Avattu?', props.auki)
                                }}
                                style={{ marginRight: 5 }}>
                                {tilanneKuvake}
                            </IconButton>

                            <ListItemText primary={apuKuvaus} secondary={apuTiedot} />
                            <ListItemIcon>
                                <IconButton
                                    component={Link}
                                    to={`/muokkaa/${idx}`}>
                                    <EditIcon />
                                </IconButton>
                                <IconButton
                                    component={Link}
                                    to={`/poista/${tiketti.id}`}
                                >
                                    <DeleteForeverIcon />
                                </IconButton>
                                <IconButton component={Link} to={`/muokkaa/${idx}`}>
                                    {seurausikoni}
                                </IconButton>
                            </ListItemIcon>
                        </ListItem>
                    )
                })}
            </List>

    )
}

export default TikettiLista